<script>
import request from "@/utils/request";

export default {
  data() {
    return {
      dialogVisible: false,
      dialogVisible2: false,
      dialogVisible3: false,
      dialogVisible4: false,
      dialogVisible5: false,
      dialogVisible6: false,
      dialogVisible7: false,
      dialogVisible8: false,
      dialogVisible9: false,
      tableData:[],
      currentPage:1,
      pageSize:10,
      search:"",
      detail:{},
      detail2:{},
      detail3:{},
      detail4:{},
      detail5:{},
      detail6:{},
      detail7:{},
      detail8:{},
      detail9:{},
    };
  },
  created() {
  this.load()
  },
  methods: {
    load(){
      request.get("/teacher", {
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          search: this.search
        }
      }).then(res => {
        this.tableData = res.data.records
        this.detail=this.tableData[0]
        this.detail2=this.tableData[1]
        this.detail3=this.tableData[2]
        this.detail4=this.tableData[3]
        this.detail5=this.tableData[4]
        this.detail6=this.tableData[5]
        this.detail7=this.tableData[6]
        this.detail8=this.tableData[7]
        this.detail9=this.tableData[8]
      })
    },
    // news(){
